body {
  height: 100vh;
  margin: 0;
}

body, .stopwatch {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__Center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: 0;
}

.stopwatch__minArr, .stopwatch__secArr {
  position: absolute;
}

.stopwatch__minArr {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-top: -20vmin;
  animation: 3600s steps(60, end) infinite runtime;
}

.stopwatch__secArr {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-top: -38vmin;
  animation: 60s linear infinite runtime;
}

.stopwatch--speed-up .stopwatch__secArr {
  animation: 10s linear infinite runtime;
}

.stopwatch--speed-up .stopwatch__minArr {
  animation: 600s steps(60, end) infinite runtime;
}

@keyframes runtime {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
